.app-container {
    display: grid;
    grid-template-columns: 200px 1fr;
    background-color: #395144;
}
.content {
    padding: 20px; /* Atur sesuai kebutuhan */
}
.sidebar {
    width: 200px;
    height: 100vh;
    background-color: #395144;
    padding: 20px;
}
.side-logo{
    background-color: #395144;
}
.logo {
    font-size: 1.5em;
    font-weight: bold;
    margin-bottom: 20px;
    background-color: #395144;
}
.menu-list{
    background-color: #395144;
    list-style-type: none;
    padding: 0%;
}
.list-items{
    background-color: #395144;
    padding: 20px;
}
.menu-side a:hover{
    color: #aa8b56;
}